import * as THREE from "three";
import GUI from "lil-gui";
import { EffectComposer, EffectPass, RenderPass } from "postprocessing";
import { CurveEffect } from "./fx/CurveEffect";

class MudCurveExperience {
	#canvas;
	#options;
	#meshes;
	#sceneEls;
	#sizes;
	#scene;
	#gui;
	#camera;
	#renderer;
	#composer;
	#curveEffect;

  /**
   * @param {HTMLCanvasElement} canvas - The canvas element
   * @param {Object} options - Configuration options
	 * @param {boolean} options.autoStart - Whether to start the experience automatically
	 * @param {string} options.sceneSelector - The selector for the scene elements. Default is "[data-scene]"
	 * @param {string} options.assetSelector - The selector for the asset elements. Default is "[data-scene-asset]"
   * @param {boolean} options.updateOnScroll - Whether to update meshes on scroll instead of animation frame
	 * @param {boolean} options.showGui - Whether to show the GUI
   * @param {Object} options.uniforms - Initial uniform values
   * @param {number} options.uniforms.distance - Distance uniform value
   * @param {number} options.uniforms.strength - Strength uniform value
   */
  constructor(canvas, options = {}) {
    this.#canvas = canvas;
    this.#options = {
			autoStart: false,
			sceneSelector: "[data-scene]",
			assetSelector: "[data-scene-asset]",
      updateOnScroll: false,
			showGui: false,
      uniforms: {
        distance: 0,
        strength: 0
      },
      ...options
    };

    this.#meshes = [];
    this.#sceneEls = [];
    
    this.#sizes = {
      width: window.innerWidth,
      height: window.innerHeight
    };

    this.#setupScene();
    this.#setupCamera();
    this.#setupRenderer();
    this.#setupPostProcessing();
    this.#setupEventListeners();

		if (this.#options.autoStart) {
			this.start();
		}
  }

	/**
	 * Setup the scene
	 * @returns {void}
	 * @private
	 */
  #setupScene() {
    this.#scene = new THREE.Scene();
    this.#gui = new GUI();
    this.#gui.hide();
    
    if (this.#options.showGui) {
      this.#gui.show();
    }
  }

  /**
   * getOrthographicCameraFrustum
   * Calculates the camera's left, right, top, and bottom properties for an orthographic camera
   * based on the browser viewport size and a desired scene size.
   *
   * @param {number} sceneSize - The desired size of the scene in Three.js units.
   * @returns {Object} - An object with the following properties:
   *   - left: The left edge of the camera's view frustum.
   *   - right: The right edge of the camera's view frustum.
   *   - top: The top edge of the camera's view frustum.
   *   - bottom: The bottom edge of the camera's view frustum.
   */
  #getOrthographicCameraFrustum(sceneSize) {
    const viewportWidth = window.innerWidth;
    const viewportHeight = window.innerHeight;
    const aspectRatio = viewportWidth / viewportHeight;

    let cameraLeft, cameraRight, cameraTop, cameraBottom;

    if (aspectRatio >= 1) {
      cameraLeft = -sceneSize;
      cameraRight = sceneSize;
      cameraTop = sceneSize / aspectRatio;
      cameraBottom = -sceneSize / aspectRatio;
    } else {
      cameraLeft = -sceneSize * aspectRatio;
      cameraRight = sceneSize * aspectRatio;
      cameraTop = sceneSize;
      cameraBottom = -sceneSize;
    }

    return { left: cameraLeft, right: cameraRight, top: cameraTop, bottom: cameraBottom };
  }

	/**
	 * Setup the camera
	 * @returns {void}
	 * @private
	 */
  #setupCamera() {
    const cameraFrustum = this.#getOrthographicCameraFrustum(10);
    this.#camera = new THREE.OrthographicCamera(
      cameraFrustum.left,
      cameraFrustum.right,
      cameraFrustum.top,
      cameraFrustum.bottom,
      0.1,
      1000
    );
    this.#camera.position.set(0, 0, 10);
    this.#scene.add(this.#camera);
  }

	/**
	 * Setup the renderer
	 * @returns {void}
	 * @private
	 */
  #setupRenderer() {
    this.#renderer = new THREE.WebGLRenderer({
      canvas: this.#canvas,
      powerPreference: "high-performance",
      antialias: false,
      stencil: false,
      depth: false,
      alpha: true
    });
    this.#renderer.setSize(this.#sizes.width, this.#sizes.height);
    this.#renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
  }

	/**
	 * Setup post-processing
	 * @returns {void}
	 * @private
	 */
  #setupPostProcessing() {
    this.#composer = new EffectComposer(this.#renderer);
    this.#composer.addPass(new RenderPass(this.#scene, this.#camera));
    this.#curveEffect = new CurveEffect();
    this.#composer.addPass(new EffectPass(this.#camera, this.#curveEffect));

    // Setup GUI controls
    this.#gui.add(this.#curveEffect.uniforms.get("uDistance"), "value")
      .min(0).max(100).step(0.01).name("Distance");
    this.#gui.add(this.#curveEffect.uniforms.get("uStrength"), "value")
      .min(0).max(1).step(0.01).name("Strength");

		this.#curveEffect.uniforms.get("uDistance").value = this.#options.uniforms.distance;
		this.#curveEffect.uniforms.get("uStrength").value = this.#options.uniforms.strength;
  }

  /**
   * getElementSpecsNormalized
   * @param {HTMLElement} el
   * @returns {Array<number>} [x, y, w, h]
   */
  #getElementSpecsNormalized(el) {
    const elRect = el.getBoundingClientRect();
    const elCenter = {
      x: elRect.left + elRect.width / 2,
      y: elRect.top + elRect.height / 2 + window.scrollY
    };
    const _x = (elCenter.x / window.innerWidth) * 2 - 1;
    const _y = -((elCenter.y - window.scrollY) / window.innerHeight) * 2 + 1;
    const w = (elRect.width / window.innerWidth) * 2 + 1;
    const h = (elRect.height / window.innerHeight) * 2 + 1;

    const elVecWorld = new THREE.Vector3(_x, _y, 0).unproject(this.#camera);
    const { x, y } = elVecWorld;

    return [x, y, w, h];
  }

  /**
   * setMeshSpecs
   * @param {Object} mesh
   * @param {number} x
   * @param {number} y
   * @param {number} w
   * @param {number} h
	 * @returns {void}
	 * @private
   */
  #setMeshSpecs(mesh, x, y, w, h) {
    const { width, height } = this.#getCanvasSizeInThreeUnits();

    mesh.current.position.x = x;
    mesh.current.position.y = y;

    const scaled = (m, n) => (m * (1 - Math.abs(n))) / 2;
    let sw = scaled(width, w);
    let sh = scaled(height, h);

    mesh.current.scale.x = sw;
    mesh.current.scale.y = sh;
  }

  /**
   * getCanvasSizeInThreeUnits
   * @returns {{width: number, height: number}}
	 * @private
   */
  #getCanvasSizeInThreeUnits() {
    const canvasSize = new THREE.Vector2();
    this.#renderer.getSize(canvasSize);

    let canvasWidthInUnits, canvasHeightInUnits;

    if (this.#camera.isOrthographicCamera) {
      canvasWidthInUnits = this.#camera.right - this.#camera.left;
      canvasHeightInUnits = this.#camera.top - this.#camera.bottom;
    } else {
      throw new Error("Camera must be an OrthographicCamera");
    }

    return {
      width: canvasWidthInUnits,
      height: canvasHeightInUnits
    };
  }

  /**
   * Apply and scale material texture
   * @param {Object} mesh
   * @param {THREE.Texture} texture
   * @param {number} twidth
   * @param {number} theight
   * @param {number} fitaspect
	 * @returns {void}
	 * @private
   */
  #applyTexture(mesh, texture, twidth, theight, fitaspect) {
    texture.flipY = false;
    texture.colorSpace = THREE.SRGBColorSpace;
    texture.wrapS = THREE.RepeatWrapping;
    texture.repeat.x = -1;
    
    const aspect = twidth / theight;
    
    if (fitaspect > aspect) {
      texture.repeat.y = aspect / fitaspect;
      texture.offset.y = (1 - texture.repeat.y) / 2;
    } else if (fitaspect < aspect) {
      texture.repeat.x = (fitaspect / aspect) * -1;
      texture.offset.x = (1 - texture.repeat.x) / 2;
    }
    
    mesh.current.material.map = texture;
    mesh.current.material.transparent = true;
    mesh.current.material.needsUpdate = true;
    this.#renderer.initTexture(texture);
  }

  /**
   * initSceneElements
	 * Initialize all scene elements
	 * @returns {void}
	 * @public
   */
  initAll() {
		const sceneEls = document.querySelectorAll(this.#options.sceneSelector);		
		this.#sceneEls = Array.from(sceneEls);
    this.#sceneEls.forEach((el) => {
			this.initMesh(el);
    });
  }

	/**
	 * initMesh
	 * Initialize a mesh from a DOM element
	 * If the element already exists in the scene, it will be destroyed first.
	 * @param {HTMLElement} el
	 * @returns {void}
	 * @public3
	 */
	initMesh(el) {
		if (!this.#sceneEls.find(e => e === el)) {
			this.#sceneEls.push(el);
		}

		if (this.#meshes.find(m => m.domElement === el)) {
			this.destroyMesh(existing);
		}

		const specs = this.#getElementSpecsNormalized(el);
		const mesh = {
			current: new THREE.Mesh(
				new THREE.PlaneGeometry(1, 1),
				new THREE.MeshBasicMaterial()
			),
			specs,
			domElement: el
		};   

		const asset = el.querySelector(this.#options.assetSelector);
		const elementType = asset?.tagName;

		if (asset && ["VIDEO", "IMG"].includes(elementType)) {
			const src = asset.currentSrc;
			if (src) {
				const elAspect = asset.clientWidth / asset.clientHeight;

				if (elementType === "VIDEO") {
          if (asset.readyState >= 2) {
            const texture = new THREE.VideoTexture(asset);
            const { videoWidth: width, videoHeight: height } = texture.source.data;
            this.#applyTexture(mesh, texture, width, height, elAspect);
          } else {
            asset.addEventListener("loadeddata", () => {
              const texture = new THREE.VideoTexture(asset);
              const { videoWidth: width, videoHeight: height } = texture.source.data;
              this.#applyTexture(mesh, texture, width, height, elAspect);
            });
          }
				} else {
          const loader = new THREE.TextureLoader();
          loader.loadAsync(src).then((texture) => {
            const { naturalWidth: width, naturalHeight: height } = texture.source.data;
            this.#applyTexture(mesh, texture, width, height, elAspect);
          });
				}
			}
		}
    else if (el.hasAttribute("data-scene-fill")) {
      mesh.current.material.color = new THREE.Color(el.dataset.sceneFill);
    }

		this.#setMeshSpecs(mesh, ...mesh.specs);
		this.#scene.add(mesh.current);
		this.#meshes.push(mesh);
	}
		
	/**
	 * Destroy / dispose all resources
	 * @returns {void}
	 * @public
	 */
	destroyAll() {
		this.#sceneEls = [];
		this.#meshes = [];
		this.#scene.clear();
		this.#renderer.renderLists.dispose();
	}

	/**
	 * destroyMeshFromEl
	 * Destroy the mesh associated with a DOM element
	 * @param {HTMLElement} el
	 * @returns {void}
	 * @public
	 */
	destroyMesh(el) {
		this.sceneEls = Array.from(this.#sceneEls).filter(e => e !== el);
		const mesh = this.#meshes.find(m => m.domElement === el);
		this.#meshes = this.#meshes.filter(m => m.domElement !== el);
		this.#scene.remove(mesh.current);
		mesh.current?.material?.map?.dispose();
		mesh.current?.geometry?.dispose();
		mesh.current?.material?.dispose();
	}

  /**
   * positionSceneElements
   * Position scene meshes based on position of DOM elements
	 * @returns {void}
	 * @private
   */
  #positionSceneElements() {
		if (!this.#sceneEls.length) {
			return;
		}

	  this.#sceneEls.forEach((el) => {
      const mesh = this.#meshes.find((m) => m.domElement === el);

			if (!mesh) {
				return;
			}

      mesh.specs = this.#getElementSpecsNormalized(el);
      this.#setMeshSpecs(mesh, ...mesh.specs);
    });
  }

	/**
	 * Setup event listeners
	 * @returns {void}
	 * @private
	 */
  #setupEventListeners() {
    window.addEventListener("resize", () => this.#handleResize());
    window.addEventListener("scroll", () => this.#handleScroll());
  }

	/**
	 * Handle resize
	 * @returns {void}
	 * @private
	 */
  #handleResize() {
    this.#sizes.width = window.innerWidth;
    this.#sizes.height = window.innerHeight;
    this.#positionSceneElements();
    this.#renderer.setSize(this.#sizes.width, this.#sizes.height);
    this.#renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    this.#composer.setSize(this.#sizes.width, this.#sizes.height);
  }

	/**
	 * Handle scroll
	 * @returns {void}
	 * @private
	 */
  #handleScroll() {
    if (this.#options.updateOnScroll) {
      this.#positionSceneElements();
    }
  }

	/**
	 * Tick
	 * @returns {void}
	 * @private
	 */
  #tick() {
    this.#composer.render();
    if (!this.#options.updateOnScroll) {
      this.#positionSceneElements();
    }
    window.requestAnimationFrame(() => this.#tick());
  }

	/**
	 * Start the experience
	 * @returns {void}
	 */
  start() {   
    this.#tick();
  }
}

export default MudCurveExperience;