class Pager {
	constructor() {
		const defaultPage = document.querySelector("template");
		console.log(defaultPage);

		this.showPage(defaultPage);

		const navLinks = document.querySelectorAll("button[data-page]");

		navLinks.forEach((link) => {
			link.addEventListener("click", (event) => {
				const page = document.querySelector(
					`template#page-${event.target.dataset.page}`
				);
				this.showPage(page);
			});
		});
	}

	showPage(page) {
		// Dispatch pre-navigate event
		const navigateEvent = new CustomEvent("navigate", {
			detail: {
				page,
			},
		});
		window.dispatchEvent(navigateEvent);

		// Get the page fragment
		const pageFragment = page.content.cloneNode(true);
		console.log(pageFragment);

		// Get the main element
		const contents = document.querySelector("section.contents");
		// Clear the main element
		contents.innerHTML = "";
		// Append the page fragment to the contents element
		contents.appendChild(pageFragment);

		const navigatedEvent = new CustomEvent("navigated", {
			detail: {
				page,
			},
		});

		setTimeout(() => {
			window.dispatchEvent(navigatedEvent);
		}, 10);
	}
}

export default Pager;
